.board {
  margin: auto;
  padding: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  /* border: 1px solid black; */
}
.activeCell {
  width: 50px;
  height: 50px;
  background-color: black;
}
.inactive–°ell {
  width: 50px;
  height: 50px;
  background-color: rgb(112, 110, 110);
}
.board_cell {
  width: 11.9%;
  height: 12.5%;
}
.board_position {
  display: flex;
  align-items: center;
}
.numbs {
  margin: 0 0.6%;
}
.letters {
  margin-left: 2%;

}
.letters div {
  display: flex;
  justify-content: center;
  width: 13%;
  margin: 5px 0;
}
.board_cell_promote {
  width: 50%;
  height: 50%;
}
.squaere_black {
  background: rgb(75, 72, 72);
  width: 100%;
  height: 100%;
}
.squaere_white {
  background: rgb(199, 195, 195);
  width: 100%;
  height: 100%;
}
.squaere_bla{
  z-index: 111;
  width: 100%;
  height: 100%;
   background: rgb(81, 193, 207);
}
.board_item {
  width: 100%;
  height: 100%;
}
.chess_piece {
  cursor: grab;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.pieceImg {
  max-width: 100%;
  max-height: 100%;
}

.cursor_pointer {
  cursor: pointer;
}
.imgG {
  max-width: 100px;
}
